.main-layout {
    padding-left: 8px;
}

.flex {
    display: flex;
}

.column {
    flex-direction: column;
}

.align-center {
    align-items: center;
}

.justify-center {
    justify-content: center;
}

.space-between {
    justify-content: space-between;
}

.grow {
    flex-grow: 1;
}

.wrap {
    flex-wrap: wrap;
}